import '../scss/style.scss';
import BODY from './_getBody';
import ElementTemplate from './_insertElem';
import { row1 } from './_ruKeyboard';

const WRAPPER = new ElementTemplate('div', 'wrapper', BODY).appendElement();

const MAIN = new ElementTemplate('main', 'main', WRAPPER).appendElement();

const SECTION = new ElementTemplate('section', 'section', MAIN).appendElement();

const H1 = new ElementTemplate('h1', 'section__title', SECTION).insertText('Virtual keyboard');

const FORM = new ElementTemplate('form', 'section__form form', SECTION).appendElement();

const TEXTAREA = new ElementTemplate('textarea', 'form__textarea', FORM).appendElement();

const P = new ElementTemplate('p', 'section__info', SECTION).insertText('OS Windows. Switch language buttons: Win + space.');

const KEYBOARD = new ElementTemplate('div', 'section__keyboard keyboard', SECTION).appendElement();

const KEYBOARD_ROW = new ElementTemplate('div', 'keyboard__row', KEYBOARD).appendElement();

for (let key of row1) {
	if (key === 'Backspace') {
		new ElementTemplate('div', 'keyboard__button_special-button keyboard__backspace-button', KEYBOARD_ROW).insertText(key);
	} else {
		new ElementTemplate('div', 'keyboard__button', KEYBOARD_ROW).insertText(key);
	}
}

